.login-root {
  position: absolute;
  width: 150px;
  left: 85%;
  background-color: rgb(0, 188, 212);
}
.login-slide {
  background-color: lightgray;
  position: absolute;
  display: none;
  height: 60px;
  -webkit-animation: slide 1s forwards;
          animation: slide 1s forwards;
  z-index: -1;
}

@-webkit-keyframes slide {
  100% {
    top: 110%;
  }
}

@keyframes slide {
  100% {
    top: 110%;
  }
}



